<template>
  <div class="release-wrap">
    <div class="title">{{ $t('admin.groupContent') }}</div>
    <van-field
      translate="no"
      class="text-wrap"
      :placeholder="$t('common.limit100')"
      maxlength="300"
      rows="4"
      type="textarea"
      show-word-limit
      v-model="message"
      :autosize="{ minHeight: 260 }"
    />
    <div class="notice">
      {{ $t('admin.willSend') }}
      <span translate="no">{{ myInfo.totalPeople || 0 }}</span>
      {{ $t('admin.memberCount') }}
      <!-- 将发送给您 <span>3代</span> 好友共计
      <span>{{ myInfo.totalPeople || 0 }}人</span> -->
    </div>
    <div class="bottom">
      <div class="pay">
        <div class="left" v-if="fasle">
          <div class="name">
            {{ $t('admin.send') }}
            <!-- 费用 -->
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#star1"></use>
            </svg>
          </div>
          <!-- <div class="num">{{ parseInt(myInfo.totalCost) || 0 }}SFO</div> -->
        </div>
        <div class="button" @click="handleSendMessage">
          {{ $t('admin.send') }}
        </div>
      </div>
      <div class="notice" v-if="false">
        <!-- *链上操作存在延迟，支付后请等待10分钟 -->
      </div>
    </div>
  </div>
</template>

<script>
import { handleMessageInfo, handleSendMessage } from '@/services/teamLeader'
import { handleMining } from '@/services/warehourse'
export default {
  name: '',
  data () {
    return {
      message: '', // 消息
      cvrBalance: 0,
      myInfo: {}
    }
  },
  methods: {
    async getSFOBalance () {
      const resp = await handleMining()
      if (!resp.success) return
      resp.data.assetsList.forEach(item => {
        if (item.name === 'SFO') {
          this.cvrBalance = this.$gbUtils.formatBalance(item.amount)
          return true
        }
      })
    },
    // 获取信息
    async getInfo () {
      const resp = await handleMessageInfo()
      if (!resp.success) return
      this.myInfo = resp.data
    },
    // 发送消息
    async handleSendMessage () {
      if (!this.message) {
        return this.$toast('内容不能为空')
      }
      // if (this.cvrBalance < this.myInfo.totalCost) {
      //   return this.$toast('余额不足')
      // }
      const resp = await handleSendMessage({
        message: this.message
      })
      if (!resp.success) return
      this.$toast('success')
      this.$gbUtils.handleToPage('/messageList')
    }
  },
  mounted () {
    this.getInfo()
    this.getSFOBalance()
  }
}
</script>

