import request from '@/utils/request'

// 团队长首页
export const handleLeaderInfo = (params) => {
  return request('GET', '/dapp/group/index', params)
}

// 收益列表
export const handleRewardList = (params) => {
  return request('GET', '/dapp/mining/list', params)
}

// 发送消息页
export const handleMessageInfo = (params) => {
  return request('GET', '/dapp/group/sendMessage', params)
}

// 消息列表
export const handleMessageList = (params) => {
  return request('GET', '/dapp/group/list', params, true, true)
}

// 发送消息
export const handleSendMessage = (params) => {
  return request('POST', '/dapp/group/sendMessage', params, true, true, 0)
}

// 团队长明细
export const handleLeaderReward = (params) => {
  return request('GET', '/dapp/mining/adminList', params, true, true, 0)
}
